import React, { Component } from 'react'
import axios from 'axios';

const backendUrl = process.env.REACT_APP_BACKEND_URL;

export default class Login extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            form:{
                email:'',
                password:''
            }
             
        }
    }

    peticionPost=()=>{

        // Request API.
        axios
        .post(`${backendUrl}/auth/local`, {
            identifier: this.state.form.email,
            password: this.state.form.password,
        })
        .then(response => {
            // Handle success.
            console.log('Well done!');
            console.log('User profile', response.data.user);
            console.log('User token', response.data.jwt);
        })
        .catch(error => {
            // Handle error.
            console.log('An error occurred:', error.response);
        });
    }

    handleChange=async(e)=>{
        e.persist();
        this.setState({
            form:{
                ...this.state.form,
                [e.target.name]:e.target.value
            }
        })
    }
    
    render() {
        const form = this.state.form;
        return (
            <div className="login">                
                <div className="card">
                    <div className="card-header">Loggu√©ate</div>
                    <div className="card-body form-group">
                        <label htmlFor="email">email</label>
                        <input type="email" className="form-control" id="email" name="email" value={form?form.email:''} placeholder="digita tu correo electr√≥nico" onChange={this.handleChange}/>
                        <label htmlFor="password">password</label>
                        <input type="password" className="form-control" id="password" name="password" value={form?form.password:''} placeholder="digita tu contrase√±a" onChange={this.handleChange}/>
                        <button className="btn btn-primary" onClick={()=>this.peticionPost()}>Ingresar</button>
                    </div>
                </div>
            </div>
        )
    }
}
