import React, { Component } from 'react';
import axios from 'axios';

const backendUrl = process.env.REACT_APP_BACKEND_URL;



export default class register extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            form:{
                email:'',
                password:''
            }
             
        }
    }
    handleChange=async(e)=>{
        e.persist();
        await this.setState({
            form:{
                ...this.state.form,
                [e.target.name]:e.target.value
            }
        })
        console.log(this.state.form)
    }

    peticionPost=()=>{
        // Request API.
    // Add your own code here to customize or restrict how the public can register new users.
    axios
    .post(`${backendUrl}/auth/local/register`, {
    username: this.state.form.email,
    email: this.state.form.email,
    password: this.state.form.password,
    })
    .then(response => {
    // Handle success.
    this.setState({form:null})
    console.log('Well done!');
    console.log('User profile', response.data.user);
    console.log('User token', response.data.jwt);
    })
    .catch(error => {
    // Handle error.
    console.log('An error occurred:', error.response);
    });
    }


    componentDidMount() {
    }
    

    
    render() {
        const form = this.state.form;
        return (
            <div className="register">                
                <div className="card">
                    <div className="card-header">Reg√≠strate</div>
                    <div className="card-body form-group">
                        <label htmlFor="email">email</label>
                        <input type="email" className="form-control" id="email" name="email" value={form?form.email:''} onChange={this.handleChange}/>
                        <label htmlFor="password">password</label>
                        <input type="password" className="form-control" id="password" name="password" value={form?form.password:''} onChange={this.handleChange}/>
                        <button className="btn btn-primary" onClick={()=>this.peticionPost()}>Registrarse</button>
                    </div>
                </div>
            </div>
        )
    }
}
